<template>
  <div class="hello">
    <van-tabbar v-model="active" :fixed="true" active-color="#f44" @change="changeTab">
      <van-tabbar-item icon="home-o" to="./home">首页</van-tabbar-item>
      <van-tabbar-item icon="shopping-cart-o" to="./shoppingCar">购物车</van-tabbar-item>
      <van-tabbar-item icon="friends-o" to="./myPage">个人中心</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      active:0
    }
  },
  watch: {
    '$route':{
      handler(val){
        console.log(val.name);
        if(val.name == 'home'){
          this.active=0
        }
        if(val.name == 'shoppingCar'){
          this.active=1
        }
        if(val.name == 'myPage'){
          this.active=2
        }
        // if(this.active == 'home'){
        //   this.changeHome()
        // }
      },
      immediate:true
    }
  },
  methods:{
    changeTab(){
      console.log(this.active);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello{
    margin-top: 4rem;
  }
</style>



