<template>
  <div class="layout">
    <router-view></router-view>
    <span v-if="$route.name === 'home' || $route.name === 'shoppingCar' || $route.name === 'myPage'">
      <navigation></navigation>
    </span>
  </div>
</template>
<script>
  import navigation from './navigation'
  export default{
    components:{navigation},
    data(){
      return {}
    },
    created(){
      console.log(this.$route);
    }
  }
</script>



